import { Fragment } from "react";
import classes from './Credentials.module.css';

const Login = (props) => {

    const onSubmitHandler = (e) => {
        e.preventDefault();
    }

    return (
        <Fragment>
            <div className={classes.login}>
                <div className={classes.header}>
                    <p><b>SIGN IN</b></p>
                </div>
                <div className={classes.form}>
                    <form>
                        <input className={classes.inputBox} type="email" placeholder="E-mail address" />
                        <input className={classes.inputBox} type="password" placeholder="Password" />
                        <button className={classes.submit} onClick={onSubmitHandler}><b>LOGIN</b></button>
                    </form>
                </div>
                <div className={classes.footer}>
                    <u>Forgot Password?</u> or <u onClick={props.content}>Sign Up!</u>
                </div>
            </div>
        </Fragment>
    );
}

export default Login;