import { Switch, Route } from 'react-router-dom';
import LandingPage from './LandingPage/LandingPage';
import SignUp from './Credentials/SignUp';

function App() {
  return (
    <Switch>
      <Route path='/' exact>
        <LandingPage />
      </Route>
      <Route path='/signup' exact>
        <SignUp />
      </Route>
    </Switch>
  );
}

export default App;
